<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup box__shadow__02">
      <div class="popup__top">
        <h4>치료약 카테고리 {{ popupState }}</h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <ul>
        <li>
          <dl>
            <dt class="f__medium">과목</dt>
            <dd>
              <select name="" id="" v-model="paramObj.subjectId">
                <option value="">과목전체</option>
                <option
                  v-for="subject in medicineSubjectList"
                  :key="subject.index"
                  :value="subject.codeId"
                >
                  {{ subject.codeName }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">카테고리명</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="paramObj.categoryName"
              />
            </dd>
          </dl>
        </li>
        <li>
          <button class="custom__btn">
            <span>
              <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
            </span>
            <p>저장</p>
          </button>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      paramObj: {
        subjectId: "",
      },
    };
  },
  props: {
    popupEventFlag: String,
    popupState: String,
    selectedObj: [Object, Boolean],
    medicineSubjectList: Array,
  },
  watch: {
    popupState() {
      if (this.popupState === "수정") {
        this.paramObj = this.selectedObj;
        if (this.$env == "development") console.log(this.paramObj);
      } else if (this.popupState === "추가") {
        this.paramObj = {};
      }
    },
  },
  computed: {
    getRemedySubject() {
      return this.$store.state.treatmentManagement.subject;
    },
  },
  methods: {
    popupCloseClick() {
      this.$emit("popupClose", "Close");
    },
  },
};
</script>
