<template>
  <main class="remedy__category__main">
    <MedicineCategoryPopup
      :popupEventFlag="popupEventFlag"
      @popupClose="popupToggle"
      :popupState="popupState"
      :selectedObj="selectedRowObj"
      :medicineSubjectList="medicineSubjectList"
    />
    <section class="box__shadow__01 vertical__scroll">
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["치료약 카테고리"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["치료약 카테고리"].subTitle }}
      </h5>
      <div class="grid__button__wrap">
        <div class="search__wrap">
          <select
            name=""
            id=""
            ref="subjectSelector"
            v-model="subjectIdFilter"
            @change="gridDataFiltering"
          >
            <option value="">과목전체</option>
            <option
              v-for="subject in medicineSubjectList"
              :key="subject.index"
              :value="subject.codeId"
            >
              {{ subject.codeName }}
            </option>
          </select>
          <input
            type="text"
            class="custom__input"
            placeholder="카테고리명을 입력하세요"
            ref="categoryNameInput"
          />
          <button class="custom__btn" @click="gridDataFiltering">
            <span
              ><img src="@/assets/images/icon/ic-search.svg" alt="검색 아이콘"
            /></span>
            <p>조회</p>
          </button>
        </div>
        <button class="custom__btn" @click="popupToggle('추가')">
          <span
            ><img src="@/assets/images/icon/ic-plus.svg" alt="플러스 아이콘" />
          </span>
          <p>카테고리 추가</p>
        </button>
      </div>
      <div class="grid__wrap" @click="rowClick($event)">
        <Grid
          class="on"
          ref="gridEl"
          :cols="cols"
          :pagination="true"
          :sort="true"
          :rows="filteredGridData"
        />
      </div>
    </section>
  </main>
</template>
<script>
import { Grid } from "gridjs-vue";
import "@/assets/css/remedyCategory.css";
import MedicineCategoryPopup from "@/components/MedicineCategory/MedicineCategoryPopup";
import { getCommonCodeList } from "@/api/code";
import _ from "lodash";

export default {
  components: {
    Grid,
    MedicineCategoryPopup,
  },
  computed: {
    getRemedyCategory() {
      return this.$store.state.treatmentManagement.category;
    },
    getRemedySubject() {
      return this.$store.state.treatmentManagement.subject;
    },
  },
  data() {
    return {
      filteredGridDataBack: [],
      filteredGridData: [],
      selectedState: "",
      cols: [
        { name: "과목", id: "subjectName" },
        { name: "카테고리명", id: "codeName" },
        { name: "등록자명", id: "registerName" },
        { name: "등록일자", id: "createDate" },
        { name: "수정" },
        { name: "삭제" },
      ],
      selectedRowObj: {},
      popupState: "",
      popupEventFlag: "Close",
      medicineSubjectList: [],
      medicineCategoryList: [],
      aStopMedicineCategoryList: [],
      mStrongMedicineCategoryList: [],
      subjectIdFilter: "",
    };
  },
  mounted() {
    this.filteredGridDataBack = this.getRemedyCategory;
    this.filteredGridData = this.getRemedyCategory;
    this.getMedicineSubjectList();
    this.getMedicineCategoryList();
  },
  methods: {
    getMedicineSubjectList() {
      let searchQuery = "mainCommCode=APP_DIV&useYn=Y";
      getCommonCodeList(searchQuery).then((response) => {
        this.medicineSubjectList = response.data.response.codeList;
      });
    },
    getMedicineCategoryList() {
      let searchQuery = "mainCommCode=MACA&useYn=Y";
      getCommonCodeList(searchQuery)
        .then((response) => {
          this.aStopTreatmentCategoryList = response.data.response.codeList;
        })
        .then(() => {
          let searchQuery = "mainCommCode=MMCA&useYn=Y";
          getCommonCodeList(searchQuery)
            .then((response) => {
              this.mStrongTreatmentCategoryList =
                response.data.response.codeList;
            })
            .then(() => {
              this.treatmentCategoryList = _.concat(
                this.aStopTreatmentCategoryList,
                this.mStrongTreatmentCategoryList
              );
              this.treatmentCategoryList.forEach((category) => {
                if (category.mainCommCode === "MACA") {
                  category.subjectName = "알코올 중독 치료";
                  category.subjectId = "APP001";
                } else if (category.mainCommCode === "MMCA") {
                  category.subjectName = "인지장애 치료";
                  category.subjectId = "APP002";
                }
              });
              this.filteredGridDataBack = this.treatmentCategoryList;
              this.filteredGridData = this.treatmentCategoryList;
            });
        });
    },
    popupToggle(evtName) {
      if (evtName === "추가") this.selectedRowObj = false;
      this.popupState = evtName;
      this.popupEventFlag === "Close"
        ? (this.popupEventFlag = "Open")
        : (this.popupEventFlag = "Close");
    },
    gridDataFiltering() {
      const selectedSubject =
        this.$refs.subjectSelector.options[
          this.$refs.subjectSelector.selectedIndex
        ].textContent.trim();

      this.filteredGridData = this.filteredGridDataBack;

      if (selectedSubject !== "과목전체") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.subjectName.trim() === selectedSubject.trim()
        );
      }

      if (this.filteredGridData.length > 0) {
        const nameFilter = [];
        this.filteredGridData.forEach((obj) => {
          if (obj.codeName.includes(this.$refs.categoryNameInput.value)) {
            nameFilter.push(obj);
          }
        });
        this.filteredGridData = nameFilter;
      }
    },
    rowClick(param) {
      const obj = param.path[0].parentNode;
      this.selectedState = param.path[0].getAttribute("data-column-id");
      switch (this.selectedState) {
        case "수정":
          this.selectedRowObj = {};
          this.selectedRowObj.subject = obj.childNodes[0].textContent;
          this.selectedRowObj.categoryName = obj.childNodes[2].textContent;
          this.selectedRowObj.registrantName = obj.childNodes[3].textContent;
          this.popupToggle("수정");
          break;
        case "삭제":
          if (this.$env == "development") console.log("삭제");
          break;
      }
    },
    gridSizeCheck() {
      // No button & No Check box
      if (this.filteredGridData.length > 0) {
        this.$refs.gridEl.$el.classList.add("on");
      } else {
        this.$refs.gridEl.$el.classList.remove("on");
      }
    },
  },
};
</script>
